.left-nav-vert {
    color: $ms-color-themeDarker;
    background-color: $ms-color-themeLighterAlt;

    ul.nav {
        position: relative;
        margin: 0;
        padding: 0;
        top: 0px;
        list-style: none;

        span.nav-item-title {
            position: absolute;
            margin-left:16px;

        }
        ul {
            margin: 0;
            padding: 0;
            list-style: none;
        }
        li {
            a {
                &:link, &:visited, &:active, &:hover {
                    color: $ms-color-themeDarker;
                }
            }
            &:hover {
                background-color: $ms-color-themeLight;
            }
        }
        .ms-SearchBox-field {
            width: 100%;
            margin-bottom: 0px;
        }

        .ms-SearchBox {
            width: 100%;
            margin: 0px;
            z-index: 1;
            background-color: #fff;
            &.ms-SearchBox--commandBar {
                width:100%;

                top: 0;

            }
        }

        .menuItem-actions {
            z-index: 1;
            float: right;
            padding: 0px;
            margin: 0px;
            margin-right: 4px;
            height: 40px;

            .ms-ListItem-action {
                width: 20px;
                height: 20px;
                padding: 0px;
                margin-top: 10px;

            }

        }
        // .subMenu {
            //     li {

            //         a {

            //         }
            //         // &:hover {
            //         //     text-decoration: none;
            //         //     background-color: $ms-color-themePrimary;
            //         //     color: $ms-color-white;
            //         // }

            //     }
        // }


    }

    .subMenu-wrap {
        -ms-overflow-style: none;
        overflow: auto;
        overflow-x: hidden;
        overflow-y: scroll;
        max-height: 0px;
        margin-left: 0px;
        -webkit-transition: max-height 300ms ease-in-out;
        -o-transition: max-height 300ms ease-in-out;
        transition: max-height 300ms ease-in-out;

        background-color: $ms-color-white;
        border-width: 0px 2px 0px 4px;
        border-style: solid;
        border-color: $ms-color-themeLighter;
        &.is-open  {
            border-width: 0px 2px 2px 4px;
            border-style: solid;
            border-color: $ms-color-themeLighter;
            display: block;
            max-height: 300px;
            -webkit-transition: max-height 300ms ease-in-out;
            -o-transition: max-height 300ms ease-in-out;
            transition: max-height 300ms ease-in-out;

            &::-webkit-scrollbar {

                display: none;
            }


        }
        &::-webkit-scrollbar {

            display: none;
        }

    }



    .getSubMenu {

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        position: relative;
        display: block;
        text-decoration: none;
        padding: 10px 14px;
        &:hover {
            // color: $ms-color-white;
            background-color: $ms-color-themeLighter;
        }
        a:link {
            color: $ms-color-themeDarker;
            // &:hover {
            //     color: $ms-color-white;
            //     background-color: $ms-color-themePrimary;
            // }
        }
        &.is-open {
            background-color: $ms-color-themePrimary;
            color: $ms-color-white;
            a {
                &:link, &:visited, &:active, &:hover {
                    color: $ms-color-white;
                    background-color: $ms-color-themePrimary;
                }

            }
        }
    }

    .subMenuItem {
        height: 40px;
        -webkit-transition: max-height 300ms ease-in-out;
        -o-transition: max-height 300ms ease-in-out;
        transition: max-height 300ms ease-in-out;

        &.is-selected {
            text-decoration: none;
            background-color: $ms-color-themeLight;
            -webkit-transition: max-height 300ms ease-in-out;
            -o-transition: max-height 300ms ease-in-out;
            transition: max-height 300ms ease-in-out;


            &:hover {
                background-color: $ms-color-themeLight;
            }
        }
        a:link {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            position: relative;
            display: block;
            text-decoration: none;
            padding: 10px 22px 10px 10px;
            color: $ms-color-black;
        }
        &::-webkit-scrollbar {

                display: none;
        }
    }
}
