.modernList {
    // overflow-y: scroll;
    // -ms-overflow-style: none;
    // overflow: auto;
    // overflow-x: hidden;
    padding: 0px;
    // &::-webkit-scrollbar {
    //     /* This is the magic bit */
    //     display: none;
    // }

    .ms-CommandButton-button, .ms-CommandButton-splitIcon {
        color: $ms-color-themePrimary;
    }


    .titleWrap {
        width: 100%;
        display: inline-block;
        white-space: nowrap;
        padding: 0px 0;
        margin: 0px 0;


        h1.title {
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            @include ms-font-xl;

            list-style-type: none;
            // margin: 0;
            // padding: 0;
            font-weight: 100;
            font-size: 21px;
            color: #333;
            display: inline-block;
            // padding: 0 4px;
            vertical-align: top;
            margin: 4px 0px;

            .filecount {
                max-width: 420px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                display: inline-block;
                color: $ms-color-themePrimary;
                position: relative;
                @include ms-font-s;
                vertical-align: middle;

            }
            .list-desc {

                color: $ms-color-themePrimary;
                position: relative;
                @include ms-font-l;
                width: 120px;
                max-width: 120px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;

            }
        }

    }
    .list-header {

        @include ms-font-xl;
        @include ms-fontWeight-light;
        // margin: 10px 0 1px;
        .ms-Breadcrumb {
            display: inline-block;
        }
        .ms-Breadcrumb-listItem {

            &:last-of-type {
              .ms-Breadcrumb-chevron {
                display: inline;
              }
            }
          }

    }





    .ms-CommandBar {
        // border-bottom:1px solid $ms-color-themePrimary;
        height: auto;
        max-height:44px;
        background-color: inherit;
        overflow: hidden;
        -webkit-transition: max-height .5s ease;
        -moz-transition: max-height .5s ease;
        -ms-transition: max-height .5s ease;
        -o-transition: max-height .5s ease;
        transition: max-height .5s ease;
        .ms-CommandButton + .ms-CommandButton {
            margin-left: 0px;

            @media only screen and (max-width: $ms-screen-md-max) {
            margin-left: 0px;
            }
        }
        .listDetails {
            margin: 8px 0px;
            display: block;
            position: relative;


        }

        &.is-details {
            max-height: 200px;
            -webkit-transition: max-height .5s ease;
            -moz-transition: max-height .5s ease;
            -ms-transition: max-height .5s ease;
            -o-transition: max-height .5s ease;
            transition: max-height .5s ease;

            .listDetails {

                display: block;
                position: relative;


            }
        }

        .ms-CommandBar-sideCommands {
            .ms-CommandButton--actionButton {
                .ms-CommandButton-button {
                    width: 40px;
                }
                border: none;
            }

            @media only screen and (min-width: $ms-screen-min-xl) {
              padding-right: 0px;
            }
          }

    }


}

.modernFileList {


    clear : none;
    width : 100%;
    // .wdItemRow {

    //     // border-left: 2px solid $ms-color-white;
    // }
    &.is-active {

        .wdItemRow {

            // border-left: 2px dashed $ms-color-themeLighterAlt;


            max-height: 300px;

            -webkit-transition: max-height .5s ease;
            -moz-transition: max-height .5s ease;
            -ms-transition: max-height .5s ease;
            -o-transition: max-height .5s ease;
            transition: max-height .5s ease;
            &.compact {
                max-height: 300px;
            }

        }
    }

    .wdItemRow {

        display: inline-block;
        max-height: 60px;
        overflow: hidden;

        clear : both;

        width : 100%;

        color: $ms-color-themeDarker;
        // cursor: pointer;
        @include ms-fontWeight-light;
        position: relative;

        padding: 6px;
        padding-right: 28px;


        -webkit-transition: max-height .5s ease;

        -moz-transition: max-height .5s ease;
        -ms-transition: max-height .5s ease;
        -o-transition: max-height .5s ease;
        transition: max-height .5s ease;

        // border-top: 1px solid $ms-color-themeLighterAlt;
        // border-left: 2px solid $ms-color-white;
        a {
            color: $ms-color-themeDarker;
            text-decoration: none;
            font-weight: $ms-font-weight-semibold;

        }
        .toggle-details2 {
            margin: 0px;
            position: absolute;
            right: 0;
            top: 0;
            width: 24px;
            height: 60px;
            color: $ms-color-themePrimary;
            visibility: hidden;

            &:hover {
                background: $ms-color-themeLighterAlt;
                color: $ms-color-themeDarker;
            }
            .ms-CommandButton-icon {
                margin: 0px;
                font-size: 30px;
                line-height: 60px;
                width: 24px;

            }

        }
        &.compact {
            max-height: 36px;
        }
        &.is-drag {
            max-height: 300px;
            background-color: $ms-color-themeLighterAlt;
            border: 2px dashed $ms-color-themePrimary;
            -webkit-transition: max-height .5s ease;
            -moz-transition: max-height .5s ease;
            -ms-transition: max-height .5s ease;
            -o-transition: max-height .5s ease;
            transition: max-height .5s ease;
        }
        &.is-active {

            max-height: 300px;
            background-color: $ms-color-themeLighterAlt;

            -webkit-transition: max-height .5s ease;
            -moz-transition: max-height .5s ease;
            -ms-transition: max-height .5s ease;
            -o-transition: max-height .5s ease;
            transition: max-height .5s ease;

            // border-left: 2px solid $ms-color-themeLighterALt;
            .actionsVisBlock {

                visibility: visible;
            }
            .itemDetailsControl {
                // margin-right: -24px;
                visibility: visible;


            }
        }

        // &:hover, &:focus, &:active {
              /* Safari */
        &.peek {
            max-height: 300px;
            -webkit-transition: max-height .5s ease;
            -webkit-transition-delay: .6s;
            -moz-transition: max-height .5s ease;
            -ms-transition: max-height .5s ease;
            -o-transition: max-height .5s ease;
            transition: max-height .5s ease;
            transition-delay: .6s;
        }
        .itemTitle {
            margin-left: 28px;

            // padding-right: 120px;
            h1 {
                @include ms-font-l;
                @include ms-fontWeight-light;
                color: $ms-color-themeDarker;

                margin-top: 0px;
                margin-bottom: 4px;

                position: relative;
                margin-right: 180px;

                padding-right: 0px;


                img {
                    position: relative;
                    top: 3px;
                }
                i.ms-Icon {
                    @include ms-fontColor-themePrimary;
                }
            }
        }
        .metaText-btm-rt {
            font-weight: $ms-font-weight-semilight;
            font-size: $ms-font-size-xs;

            // position: absolute;
            width:100%;
            // right: 26px;
            top: 60px;
            text-align: right;
            // margin-left: 28px;
            .itemMetaData {
                display: inline-block;
                text-align: right;
                @include ms-font-s;
                width: auto;
                max-width: 600px;
                margin: 0;
                padding: 0;
                list-style: none;
                margin-right:26px;
                  li {
                    padding-left: 0px;
                    margin: 0px;
                      max-width: 100px;
                      display: inline-block;
                      overflow: hidden;
                      text-overflow: ellipsis;
                      white-space: nowrap;
                      line-height: 15px;
                      a:href {
                        color: $ms-color-themeDarker;
                        &:link {
                            text-decoration: none;
                        }

                      }
                  }

                }
        }



        .ms-CheckBox {
            box-sizing: border-box;
            color: $ms-color-neutralPrimary;
            font-family: $ms-font-family-base;
            font-size: $ms-font-size-m;
            font-weight: $ms-font-weight-regular;
            min-height: 36px;
            position: relative;
            display: block;
            float: left;


            .ms-Label {
            //   font-size: $ms-font-size-m;
            //   padding: 0 0 0 26px;
            //   cursor: pointer;
            display: none;
            }
          }




          .detailWrap {
            padding: 4px;
            margin: 0px;
            margin-left: 64px;

            ul {
                margin: 0;
                padding: 0;
                list-style: none;


                &.itemFields {

                    display: inline-block;

                    li {
                        display: inline-block;
                        a {

                            &:link {
                                text-decoration: none;
                            }

                        }
                    }

                }
                // &.itemData {
                //     @include ms-font-s;
                //     float: right;
                //     width: auto;
                //     max-width: 204px;
                //    li {
                //         width: 100px;
                //         max-width: 100px;
                //         display: inline-block;
                //         overflow: hidden;
                //         text-overflow: ellipsis;
                //         white-space: nowrap;
                //         a {
                //             color: $ms-color-themeDarker;
                //             &:link {
                //                 text-decoration: none;
                //             }

                //         }
                //     }


                // }
            }

        }

        // &:nth-child(even){
        //     background-color: $ms-color-neutralLighter;
        // }
        &:hover{
            background-color: $ms-color-themeLighterAlt;
            .actionsVisBlock {

                visibility: visible;
            }
            .itemDetailsControl {
                // margin-right: -24px;
                visibility: visible;


            }
        }
    }
}

