 .phx-form-upload {
         margin-top : 10px;
         padding-right: 10px;
        .ms-Button {
            margin-top: 10px;
            padding: 2px;
        }
        .wd-cabDropDown {
            label {
                display: none;
            }
        }
        fieldset {
            border-width: 0px;
            border-image: none;
        }
        label {
            display: none;
        }
        a {
            text-decoration: none;
            color: inherit;
        }
        .ms-TextField {
            height: 40px;
            display: block;

            .ms-CommandButton {
                float: left;
                width: 120px;
                max-width: 120px;
                .ms-CommandButton-button {
                    text-align: left;
                    width: 120px;
                    max-width: 120px;
                }
            }

            input[type="text"] {
                max-width: calc(100% - 120px);
                width: calc(100% - 120px);
                display: inline-block;
                margin-bottom: 10px;
                height: 40px;
            }

        }
        .search-ext-btn {
            float: right;
            margin-bottom:10px;
            a {
                text-decoration: none;
                color: inherit;
            }

        }
        .search-ext {
            clear: both;
            overflow: hidden;
            height: auto;
            max-height: 600px;
            -webkit-transition: max-height 500ms ease-out;
            -moz-transition: max-height 500ms ease-out;
            -o-transition: max-height 500ms ease-out;
            transition: max-height 500ms ease-out;


            &.is-closed {
                height: 0px;
                max-height: 0px;
                -webkit-transition: max-height 500ms ease-out;
                -moz-transition: max-height 500ms ease-out;
                -o-transition: max-height 500ms ease-out;
                transition: max-height 500ms ease-out;
            }

        }
}
.wdx-FormHideAway {

    position: absolute;
    visibility: hidden;
    background: $ms-color-white;
    // height: auto;
    max-height: 0px;
    z-index: 0;
    // overflow: hidden;
    @include ms-Grid-row;
    @include ms-md8;
    @include ms-mdOffset2;
    @include ms-fontWeight-light;

    // @include anim-verticle;

    color: $ms-color-themeDarker;
    cursor: pointer;

    &.is-active {
        overflow: visible;
        // visibility: visible;
        z-index: 1;
        box-shadow:0 2px 10px -2px rgba(0,0,0,.1);
        border: 1px solid $ms-color-themeLighter;

        // height: 420px;
        // max-height: 420px;

        // @include anim-verticle;
    }

    // &:hover, &:focus, &:active {
        //     height: 420px;
        //     max-height: 420px;
    // }


    .search-context {

        // margin: 8px 0px;
        .ms-CheckBox {
            display: inline-flex;
            margin-right: 8px;
            height: 40px;
            margin: 10px 8px 0px;
            .ms-Label {
                // font-size: $ms-font-size-s;
                padding: 0 0 0 20px;
                // cursor: pointer;
                // display: inline-block;
            }
        }
    }

    .ms-DatePicker {
        margin-top: 8px;
        label {
            display: none;
        }
    }

    .ms-SearchBox,
    .ms-SearchBox-field {
        width: 100%;
    }

}
.wdx-Form-search {
    visibility: hidden;
    position: relative;
    background: $ms-color-white;
    @include ms-Grid-col;
    @include ms-sm12;
    display: block;

    // height: auto;
    max-height: 0px;
    box-shadow:0 2px 10px -2px rgba(0,0,0,.1);
    border: 1px solid $ms-color-themeLighter;
    // @include anim-verticle;

    fieldset {
        border-width: 0px;
        border-image: none;
    }
     //    label {
     //        display: none;
     //    }
    a {
        text-decoration: none;
        color: inherit;
    }
    ul {
        list-style: none;
        padding-left: 0px;
        li {
            list-style-type: none;
        }

    }
    &.is-active {

        visibility: visible;
        z-index: 1;
        // box-shadow:0 2px 10px -2px rgba(0,0,0,.1);
        // border: 1px solid $ms-color-themeLighter;

        max-height: 420px;
        // top: 50px;
        // @include anim-verticle;
    }

}

