.wdx-app {
    .phx-List {
        overflow-y: scroll;
        -ms-overflow-style: none;
        overflow: auto;
        overflow-x: hidden;

        height: calc(100vh - 194px);
        padding: 0px;
        &::-webkit-scrollbar {
            display: none;
        }

        &.is-open {
            .ms-ListItem {
                .item-details-content {
                    display: block;
                    border-bottom: 1px solid $ms-color-themeLighter;

                }
            }
        }

    }
    .wd-ObjList {
        margin: 0px;
        padding: 0px;

        &.is-open {
            .ms-ListItem {
                .item-details-content {
                    display: block;
                    border-bottom: 1px solid $ms-color-themeLighter;

                }
            }
        }
        .titleWrap {

            h1 {

                @include ms-font-xl;
                padding: 2px 0px;
                line-height: 20px;
                margin: 0px;

                .filecount {

                    color: $ms-color-themePrimary;
                    position: relative;
                    @include ms-font-s;
                    vertical-align: middle;

                }
            }
        }
        .ms-CommandBar {
            border-bottom:1px solid $ms-color-themePrimary;
            height: auto;
            max-height:44px;
            background-color: inherit;
            overflow: hidden;
            -webkit-transition: max-height .5s ease;
            -moz-transition: max-height .5s ease;
            -ms-transition: max-height .5s ease;
            -o-transition: max-height .5s ease;
            transition: max-height .5s ease;
            .ms-CommandButton + .ms-CommandButton {
                margin-left: 0px;

                @media only screen and (max-width: $ms-screen-max-md) {
                    margin-left: 0px;
                }
            }
            .listDetails {
                margin: 8px 0px;
                display: block;
                position: relative;
            }

            &.is-details {
                max-height: 200px;
                -webkit-transition: max-height .5s ease;
                -moz-transition: max-height .5s ease;
                -ms-transition: max-height .5s ease;
                -o-transition: max-height .5s ease;
                transition: max-height .5s ease;

                .listDetails {
                    display: block;
                    position: relative;

                }
            }

            .ms-CommandBar-sideCommands {
                .ms-CommandButton--actionButton {
                    .ms-CommandButton-button {
                        width: 40px;
                    }
                    border: none;
                }

                @media only screen and (min-width: $ms-screen-min-xl) {
                    padding-right: 0px;
                }
            }

        }

    }

    .listActionsBlock {
        display: inline-block;
        margin: 0px;
        padding: 0px;
    }

}