.logoBox {
    display: inline-block;
    width: 200px;
    max-width: 200px;
    height: 50px;
    padding: 0px;
    padding-left: 5px;
    margin: 0px;

    h1 {
        display: block;
        text-indent: -9999px;
        width: 140px;
        max-width: 140px;
        height: 50px;
        padding: 0px;
        margin: 0px;
        a {
            display: block;
            text-indent: -9999px;
            width: 140px;
            max-width: 140px;
            height: 46px;
            background: url(../../../assets/images/logos/wd-logo-full-rasterize-colored-sm.png) no-repeat;
            background-size: 140px 46px;
            cursor: pointer;
            background-repeat: no-repeat;
            margin: 2px 0px;
            padding: 0px;
        }
    }
}

