@media (max-width: $ms-screen-max-md) {

    .logoBox {
        margin: 0px;
        padding: 0px;
        width: 38px;
        height: 38px;
    }

    .logoBox h1, .logoBox h1 a {

        display: block;
        text-indent: -9999px;
        width: 38px;
        height: 38px;
        background: url('../../../assets/images/logos/wd-logo-w-rast-colored-sm.png') no-repeat;
        background-size: 38px 38px;
        cursor: pointer;
        background-repeat: no-repeat;
        margin: 0px;
        padding: 0px;
    }

}
