.wdx-Canvas-body {

    @include ms-Grid-row;

    .wdx-Body-nav {
        @include ms-Grid-col;

        background: $ms-color-themeLighterAlt;
        overflow-y: scroll;
        -ms-overflow-style: none;
        overflow: auto;
        overflow-x: hidden;
        padding: 0px;

        height: calc(100vh - 62px);

        width: 44px;
        max-width: 44px;

        color: $ms-color-themeDarker;
        background-color: $ms-color-themeLighterAlt;


        -webkit-transition: max-width 500ms ease-in-out;
        -o-transition: max-width 500ms ease-in-out;
        transition: max-width 500ms ease-in-out;


        &::-webkit-scrollbar {
            display: none;
        }


        .ms-CommandButton-button:hover, .ms-CommandButton-splitIcon:hover {
            background-color: $ms-color-themeLighter;
            color: $ms-color-themeDarker;

        }




    }

    .wdx-Body-col {
        @include ms-Grid-col;

        padding: 0px;
        width: calc(50% - 22px);
        max-width: calc(50% - 22px);

        -webkit-transition: max-width 500ms ease-in-out;
        -o-transition: max-width 500ms ease-in-out;
        transition: max-width 500ms ease-in-out;
        overflow: hidden;
        // height: calc(100vh - 60px);
        .ms-CommandButton-button:hover, .ms-CommandButton-splitIcon:hover {
            background-color: $ms-color-themeLighter;
            color: $ms-color-themeDarker;

        }
        // .ms-Pivot-content {
        //     border-right: 2px solid $ms-color-themeLighterAlt;
        // }

        &.body-max {
            width: calc(100% - 44px);
            max-width: calc(100% - 44px);

            -webkit-transition: all 500ms ease-in-out;
            -o-transition: all 500ms ease-in-out;
            transition: all 500ms ease-in-out;

        }


    }

    .wdx-Body-preview {

        @include ms-Grid-col;

        height: calc(100vh - 60px);
        overflow: hidden;
        padding: 0px;
        width: calc(50% - 22px);
        max-width: calc(50% - 22px);

        -webkit-transition: max-width 500ms ease-in-out;
        -o-transition: max-width 500ms ease-in-out;
        transition: max-width 500ms ease-in-out;

        .ms-Pivot-content {

            padding-right: 8px;
            padding-left: 8px;

        }

        .wd-Preview-max--btn {

                &.ms-CommandButton {
                    display: inline-block;
                }
        }
        .wd-Preview-med--btn {

                &.ms-CommandButton {
                    display: none;
                }
        }

        .ms-CommandButton-button:hover, .ms-CommandButton-splitIcon:hover {
            background-color: $ms-color-themeLighter;
            color: $ms-color-themeDarker;

        }
    }
    .wd-app--viewer  {
        display: inline-block;
        position: relative;
        height: calc(100vh - 60px);
        padding: 0px;
        width: calc(50% - 22px);
        max-width: calc(50% - 22px);

        -webkit-transition: max-width 500ms ease-in-out;
        -o-transition: max-width 500ms ease-in-out;
        transition: max-width 500ms ease-in-out;

        .ms-Pivot-content {

            padding-right: 8px;
            padding-left: 8px;

        }

        .wd-Preview-max--btn {

                &.ms-CommandButton {
                    display: inline-block;
                }
        }
        .wd-Preview-med--btn {

                &.ms-CommandButton {
                    display: none;
                }
        }

        &.viewer-max {
            display: block;
            position: absolute;

            top: 58px;
            right: 0;
            bottom:0;
            min-width: 100%;
            width: 100%;
            max-width: 100%;
            height: 100vh;
            max-height: 100vh;
            background: #fff;

            -webkit-transition: max-width 500ms ease-in-out;
            -o-transition: max-width 500ms ease-in-out;
            transition: max-width 500ms ease-in-out;

            .wd-Preview-max--btn {

                    &.ms-CommandButton {
                        display: none;
                    }
            }
            .wd-Preview-med--btn {

                    &.ms-CommandButton {
                        display: inline-block;
                    }
            }
        }
        &.viewer-min {


            display: none;

        }


        .ms-CommandButton-button:hover, .ms-CommandButton-splitIcon:hover {
            background-color: $ms-color-themeLighter;
            color: $ms-color-themeDarker;

        }


    }



    .wdx-Body-preview.viewer-min {

        width: 0px;
        max-width: 0px;
        display: none;

    }

    .wdx-Body-preview.viewer-max {
        display: block;
        position: absolute;

        top: 58px;
        right: 0;
        bottom:0;
        min-width: 100%;
        width: 100%;
        max-width: 100%;
        height: 100vh;
        max-height: 100vh;
        background: #fff;

        // -webkit-transition: max-width 500ms ease-in-out;
        // -o-transition: max-width 500ms ease-in-out;
        // transition: max-width 500ms ease-in-out;

        .wd-Preview-max--btn {

                &.ms-CommandButton {
                    display: none;
                }
        }
        .wd-Preview-med--btn {

                &.ms-CommandButton {
                    display: inline-block;
                }
        }
    }

    &.is-open {

        .wdx-Body-nav {
            width:200px;
            max-width: 200px;

            -webkit-transition: max-width 500ms ease-in-out;
            -o-transition: max-width 500ms ease-in-out;
            transition: max-width 500ms ease-in-out;
        }
        .wdx-Body-col {
            width: calc(50% - 100px);
            max-width: calc(50% - 100px);

            -webkit-transition: max-width 500ms ease-in-out;
            -o-transition: max-width 500ms ease-in-out;
            transition: max-width 500ms ease-in-out;
            &.body-max {
                width: calc(100% - 200px);
                max-width: calc(100% - 200px);
                -webkit-transition: max-width 500ms ease-in-out;
                -o-transition: max-width 500ms ease-in-out;
                transition: max-width 500ms ease-in-out;
            }
        }
        .wdx-Body-preview {
            display: block;
            width: calc(50% - 100px);
            max-width: calc(50% - 100px);
            -webkit-transition: max-width 500ms ease-in-out;
            -o-transition: max-width 500ms ease-in-out;
            transition: max-width 500ms ease-in-out;

            &.viewer-max {
                display: block;
                position: absolute;
                left: 0px;
                right: 0px;
                width: 100%;
                max-width: 100%;
                height: 100vh;
                max-height: 100vh;
                background: #fff;

            }

            &.viewer-min {

                width: 0px;
                max-width: 0px;
                display: none;

            }

        }

    }

}


