/*- Phoenix Core v0.3+          */
/*--    Global: *,html,body     */
    // @-ms-viewport {
    //     width: device-width;
    // }
    html {
        box-sizing: border-box;
    }
    *, *:before, *:after {
        box-sizing: inherit;
    }
    body {
        @include ms-font-m;
        margin: 0px;
        overflow-y: scroll;
        -ms-overflow-style: none;
        overflow: auto;
        overflow-x: hidden;
    }

