

/* WDX Div-based Tables with
-- Collapse to Column
-- Collapse to Tabs
-- Collapse to Accordion
================================== */
.wdxTable {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 3em 0;
    padding: 0;

    .wdxTable-cell {
        flex-grow: 1;
        width: 100%;  // Default to full width
        padding: 0.8em 1.2em;
        overflow: hidden; // Or flex might break
        list-style: none;
        border: solid 1px $ms-color-themeLight;
        background: $ms-color-themeLighterAlt;
        > h1, > h2, > h3, > h4, > h5, > h6 { margin: 0; }
      }

      /* Table column sizing
      ================================== */
      &.wdxTable--2cols > .wdxTable-cell  { width: 50%; }
      &.wdxTable--3cols > .wdxTable-cell  { width: 33.33%; }
      &.wdxTable--4cols > .wdxTable-cell  { width: 25%; }
      &.wdxTable--5cols > .wdxTable-cell  { width: 20%; }
      &.wdxTable--6cols > .wdxTable-cell  { width: 16.6%; }
      &.wdxTable--7cols > .wdxTable-cell  { width: 14.4%; }
      &.wdxTable--8cols > .wdxTable-cell  { width: 12%; }
      &.wdxTable--9cols > .wdxTable-cell  { width: 11%; }











      /* Apply styles
      ================================== */
      position: relative; top: 2px; left: 2px; //compensate for border offset
      .wdxTable-cell {
        margin: -2px 0 0 -2px;  //border collapse offset

      }

      /* Cell styles
      ================================== */
      .wdxTable-cell--dark {
        background-color: $ms-color-themeDarker;
        border-color: darken($ms-color-themeDarker,10%);
        color: $ms-color-themeLighterAlt;
      }
      .wdxTable-cell--medium {
        background-color: $ms-color-themeLight;
        border-color: $ms-color-themePrimary;
      }
      .wdxTable-cell--light {
        background-color: $ms-color-themeLighterAlt;
        background-color: $ms-color-themeLighter;
      }
      .wdxTable-cell--highlight {
        background-color: $ms-color-themePrimary;
        border-color:  $ms-color-themeDarker;
      }
      .wdxTable-cell--alert {
        background-color: darkorange;
        border-color: darken(darkorange,10%);
        color: ms-color-white;
      }

      // Inherit header and footer styles
      .wdxTable-cell--head {
        background-color: $ms-color-themeLighter;
        border-color: $ms-color-themePrimary;
        color: $ms-color-themeDarker;

       }
      .wdxTable-cell--foot {
        background-color: $ms-color-themeLighter;
        border-color: $ms-color-themePrimary;
        color: $ms-color-themeDarker;
       }


      /* Responsive
      ==================================== */
      @media all and (max-width: $ms-screen-min-lg) {
        &.wdxTable--collapse  {
          display: block;
          > .wdxTable-cell { width: 100% !important; }
          > .wdxTable-cell--foot { margin-bottom: 1em; }
        }
      }

      // Non-Flex modernizer fallback
      .no-flexbox .wdxTable {
        display: block;
        > .wdxTable-cell { width: 100%; }
        > .wdxTable-cell--foot { margin-bottom: 1em; }
      }



      @media all and (max-width: $ms-screen-min-lg) {
        .hiddenSmall {display: none; }
      }


      /* Tab Styling
      ==================================== */


      .wdxTable-tabs {
        display: flex;
        flex-direction: row;
        margin-left: -2px;
        @media all and (min-width: $ms-screen-min-lg) { display: none; }
        .wdxTable-tabs--tab {
            padding: 0.6em 1em;
            margin: 0 2px 2px 0;
            text-align: center;
            background-color: $ms-color-themePrimary;
            border: solid 1px $ms-color-themePrimary;
            border-bottom-width: 0;
            color: darken($ms-color-themePrimary,20%);
            text-decoration: none;
            transition: background-color 0.1s;
            cursor: pointer;
            &:hover, &:focus {
              background-color: mix(white,$ms-color-themePrimary,10%);
              border-color: mix(white,$ms-color-themePrimary,10%);
              outline: none;
            }
            &[aria-selected="false"]:active {
              margin-top: 0.2em;
              padding-bottom: 0.4em;
            }
            &[aria-selected="true"] {
              background: mix(white,$ms-color-themePrimary,90%);
              cursor: default;
            }
          }

      }


      /* Accordion Styling
      ==================================== */

      $iconSize: 1em;

      .wdxTable-acc {
        position: relative; top: -2px; left: -2px; //compensate for border offset
        width: 100%;
        margin: 0 0 0.5em 0;
        padding: 0.6em 0.6em 0.6em ($iconSize);

        text-align: left;
        border: solid 1px mix(black,$ms-color-themePrimary,15%);
        background-color: $ms-color-themePrimary;
        font-weight: bold;
        color: darken($ms-color-themePrimary,20%);
        text-decoration: none;
        transition: background-color 0.1s;
        cursor: pointer;
        @media all and (min-width: $ms-screen-min-lg) { display: none; }
        &:hover, &:focus {
          outline: none;
          filter: contrast(150%);
        }
        &[aria-selected="true"] {
          margin-bottom: 0;
          border-bottom-left-radius: 0;
          border-bottom-right-radius: 0;
          border-bottom-width: 0;
          background: mix(white,$ms-color-themePrimary,70%);
        }
        &:before {
          content: "+";
          position: absolute;
          top: 50%;
          left: 0.3em;
          margin-top: -($iconSize/1.75);
          font-weight: normal;
          font-size: $iconSize;
          line-height: $iconSize;
          background-size: $iconSize $iconSize;
        }
        &[aria-selected="true"]:before {
          content: "-";
        }
      }

      .js-wdxTableTabs, .js-wdxTableAccordions {
        min-width: 240px;
      }

  }
