        // padding: 9px 28px 3px;
        // position: relative;
        // display: block;
        &:focus {
            outline: 0;
            outline-color: $ms-color-themeLighterAlt;
            background-color: $ms-color-themeLighterAlt;
        }
        &:hover {
        background-color: $ms-color-themePrimary;
        //   outline: 1px solid transparent;

        }

    }
    .actionsVisBlock {
        margin: 0px;
        padding: 0px;
        display: inline-block;
        visibility: hidden;
    }
