        // font-size: $ms-font-size-m;
        // line-height: 25px;
        // position: relative;
        // top: -7px;
        padding-right: 220px;
        margin-right: 2px;
        margin-left: 22px;
        ul.itemFields {
            text-align: left;
            @include ms-font-s;
            width: auto;
            margin: 0;
            padding: 0;
            list-style: none;
            margin-right:4px;
            li {
                padding-left: 0px;
                margin: 0px;
                max-width: 80px;
                display: inline-block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                line-height: 15px;
