
.wd-Panel-help {

    .ms-Button.ms-Button--compound {
        width: 100%;
        background-color: $ms-color-themeLighterAlt;
        color: $ms-color-themeDarker;
        &:hover, &:active {
            color: $ms-color-themeDarker;
            .ms-Button-description, .ms-Button-label {
                color: $ms-color-themeDarker;
            }
        }


    }
    .ms-Panel-content {
        padding-top: 20px;
    }
}
