.wdTable{

    display : table;
    clear : both;
    table-layout : fixed;
    width : 100%;

    .wdHeadRow {
        clear : both;
        empty-cells : show;
        width : 100%;
        display:table-row;

        div {
            display:table-cell;
            @include ms-font-s;
            padding: 6px;
            border-bottom: 1px solid #eaeaea;
        }
    }

    .wdItemRow {
        clear : both;
        empty-cells : show;
        width : 100%;
        display:table-row;
        color: #767676;
        cursor: pointer;
        @include ms-fontWeight-light;
        div {
            display:table-cell;
            border-bottom: 1px solid #eaeaea;
            h1 {
                display: block;
                @include ms-font-m;
                @include ms-fontWeight-semibold;
                padding: 0px;
                margin: 6px;
            }
            p {
                margin:6px;
                span {
                    @include wd-fontColor-wdPurple;
                    @include ms-fontWeight-semibold;
                }
            }

        }
        &:nth-child(even){
            background-color: #f8f8f8;
        }
        &:hover{
            background-color: #f4f4f4;
        }
    }


}
