/*******************
LeftNav
*******************/
.left-nav-main {

    > .is-closed  {
        width: 44px;
    }

    position: relative;
    width: 220px;
    overflow: hidden;
    height:100vh;
    background-color: $ms-color-themeLighterAlt;
    padding: 0px;
    color: $ms-color-themeDarker;


    ul.nav {
         position: relative;
        -webkit-transition: all 500ms ease;
        -o-transition: all 500ms ease;
        transition: all 500ms ease;
        margin: 0;
        padding: 0;
        list-style: none;

        ul {
            margin: 0;
            padding: 0;
            list-style: none;
        }

        li {
            border-bottom: 1px solid $ms-color-themeLighter;
        }
        .subMenu {
            
            a {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        a {
            position: relative;
            display: block;
            text-decoration: none;
            color: rgba(0, 0, 0, .75);
            padding: 15px;
            color: $ms-color-themeDarker;
            &:link {
                text-decoration: none;
                color: $ms-color-themeDarker;
            }
            &:hover {
                text-decoration: none;
                background-color: $ms-color-themeLight;
            }
        }
        
        .ms-Icon {
            margin-top: 4px;
        }

        .nextIco {
            position: absolute;
            top: 50%;  /* position the top  edge of the element at the middle of the parent */
            right: 15px; /* position the left edge of the element at the middle of the parent */
            transform: translate(-50%, -50%);
        }

        .backIco {
            position: absolute;
            top: 50%;  /* position the top  edge of the element at the middle of the parent */
            left: 15px; /* position the left edge of the element at the middle of the parent */
            transform: translate(-50%, -50%);
        }
        .js-back {
            padding: 15px;
            cursor: pointer;
            color: rgba(0, 0, 0, .75);
            text-align:center;
            position: relative;
            &:hover {
                color: $ms-color-themeDarker;
                background-color: $ms-color-themeLight;
            }
        }

    }

    .subMenu-wrap {
        display: none;
        position: absolute;
        top: 0;
        left: 100%;
        bottom: 0;
        width: 220px;
        -webkit-transition: all 500ms ease;
        -o-transition: all 500ms ease;
        transition: all 500ms ease;
        overflow: scroll;
        overflow-x: hidden;
        overflow-y: auto;
        height: calc(100vh - 90px);

    }

    .is-open > .subMenu-wrap {
        display: block;
        overflow: scroll;
        overflow-x: hidden;
        overflow-y: auto;

    }


    .slide-out {
        -webkit-transform: translate3d(-220px, 0, 0);
        -ms-transform: translate3d(-225px, 0, 0);
        -o-transform: translate3d(-220px, 0, 0);
        transform: translate3d(-220px, 0, 0);
    }


}