button {
  border-radius: 0px;
}
.ms-Button {
    // @include ms-u-normalize;
    // @include ms-baseFont;
    color: $ms-color-themeDarker;
    background-color: $ms-color-themeLighterAlt;
    border: 1px solid $ms-color-themeLighter;
    padding: 4px;

    &.is-hidden {
      display: none;
    }

    &:hover {
      background-color: $ms-color-themeLighter;
      border-color: $ms-color-themeLighter;

      .ms-Button-label {
        color: $ms-color-themeDarker;
      }

    }

    &:focus {
      background-color: $ms-color-themeLighter;
      border-color: $ms-color-themePrimary;
      outline: 1px solid transparent;

      .ms-Button-label {
        color: $ms-color-themeDarker;
      }
    }

    &:active {
      background-color: $ms-color-themePrimary;
      border-color: $ms-color-themeDark;

      .ms-Button-label {
        color: $ms-color-white;
      }
    }

    &:disabled,
    &.is-disabled {
      background-color: $ms-color-themeLighterAlt;
      border-color: $ms-color-themeLighterAlt;
      cursor: default;

      .ms-Button-label {
        color: $ms-color-neutralTertiary;
      }

      &:hover,
      &:focus {
        outline: 0;
      }
    }
  }

  .ms-Button-label {
    color: $ms-color-themeDarker;

  }


  //== Modifier: Primary action button
  //
  .ms-Button.ms-Button--primary {
    background-color: $ms-color-themePrimary;
    border-color: $ms-color-themePrimary;

    .ms-Button-label {
      color: $ms-color-white;
    }

    &:hover {
      background-color: $ms-color-themeDark;
      border-color: $ms-color-themeDark;
    }

    &:focus {
      background-color: $ms-color-themeDark;
      border-color: $ms-color-themeDarker;
    }

    &:active {
      background-color: $ms-color-themePrimary;
      border-color: $ms-color-themePrimary;
    }

    &:disabled,
    &.is-disabled {
      background-color: $ms-color-themeLighterAlt;
      border-color: $ms-color-themeLighterAlt;

      .ms-Button-label {
        color: $ms-color-neutralTertiary;
      }
    }
  }



  //== Modifier: Hero button
  //
  .ms-Button.ms-Button--hero {


    .ms-Button-icon {
      color: $ms-color-themePrimary;


      .ms-Icon {

        border: 1px solid $ms-color-themePrimary;

      }
    }

    .ms-Button-label {
      color: $ms-color-themePrimary;

    }

    &:hover,
    &:focus {
      .ms-Button-icon {
        color: $ms-color-themeDark;

        .ms-Icon {
          border: 1px solid $ms-color-themeDark;
        }
      }

      .ms-Button-label {
        color: $ms-color-themeDarker;
      }
    }

    &:active {
      .ms-Button-icon {
        color: $ms-color-themePrimary;

        .ms-Icon {
          border: 1px solid $ms-color-themePrimary;
        }
      }

      .ms-Button-label {
        color: $ms-color-themePrimary;
      }
    }

    &:disabled,
    &.is-disabled {
      .ms-Button-icon {
        color: $ms-color-neutralTertiaryAlt;

        .ms-Icon {
          border: 1px solid $ms-color-neutralTertiaryAlt;
        }
      }

      .ms-Button-label {
        color: $ms-color-neutralTertiary;
      }
    }
  }


  //== Modifier: Compound button
  //
  .ms-Button.ms-Button--compound {



    .ms-Button-description {
      color: $ms-color-themePrimary;

    }

    &:hover {
      .ms-Button-description {
        color: $ms-color-themeLighterAlt;
      }
    }

    &:focus {
      border-color: $ms-color-themePrimary;
      background-color: $ms-color-themeLighterAlt;

      .ms-Button-label {
        color: $ms-color-themeDarker;
      }

      .ms-Button-description {
        color: $ms-color-themePrimary;
      }
    }

    &:active {
      background-color: $ms-color-themePrimary;

      .ms-Button-description,
      .ms-Button-label {
        color: $ms-color-white;
      }
    }

  }
