.wdx-Canvas-subheader {

    height: auto;
    max-height: 10px;
    background-color: $ms-color-themeLighterAlt;
    clear: both;
    overflow: hidden;
    @include anim-verticle;

    &.is-open {
        height: 60px;
        max-height: 60px;
        @include anim-verticle;

        .ms-Grid-row {
            display: block;
        }
    }

    .wdx-Ribbon-bar {

        display: block;
        height: 10px;
        width: auto;
        display: block;
        margin: 0px;
        padding: 0px;
        background: $ms-color-themeLighterAlt;
        cursor: pointer;
        // &:hover {
        //     background: $ms-color-themeLighterAlt;
        // }
    }

    .wdx-Ribbon {

        @include ms-sm12;
        padding-left: 8px;
        padding-right: 8px;
        background: $ms-color-themeLighterAlt;
        height: 50px;
        max-height: 50px;
        overflow: hidden;
        //  border: 1px solid $ms-color-themeDarker;
        .wdx-Ribbon-col {

            display: inline-flex;
            line-height: 40px;
            height: 40px;
            margin: 4px;
            width: 80px;
            max-width: 80px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            // padding: 0px;
            border: 1px solid $ms-color-themePrimary;
            background: $ms-color-themeLighterAlt;
            color: $ms-color-themeDark;

        }

    }

    .ms-CommandBar {
        height: 50px;
        background: $ms-color-themeLighter;
        // color: $ms-color-themeLighterAlt;

        .ms-CommandButton-label {
            line-height: 50px;
            color: $ms-color-themeDarker;
        }

        .ms-CommandButton-button, .ms-CommandButton-splitIcon {
            height: 50px;
            color: $ms-color-themeDark;
            background: $ms-color-themeLighter;
        }

        .ms-CommandButton-button:active,
        .ms-CommandButton-splitIcon:active,
        .ms-CommandButton-button:hover,
        .ms-CommandButton-splitIcon:hover {
            background: $ms-color-themePrimary;
            // @include ms-bgColor-themeLighter;

            // color: $ms-color-themeLighterAlt;

            // .ms-CommandButton-label {
            //     color: $ms-color-themeLighterAlt;
            // }

        }

    }
}

