.ui-modern {

    .ctrls-block{
        padding: 4px;
        .ms-CommandButton-button {
            border-radius: 50%;
            color: $ms-color-themePrimary;
            padding: 0px;
            margin: 0px;
            height: 32px;
            width: 32px;
            border: 1px solid $ms-color-themeLighter;
            .ms-CommandButton-icon {
                // background-color: $ms-color-white;

                // border: 2px solid $ms-color-themePrimary;
                margin-right: 0px;
                .ms-Icon {
                    width: 32px;
                    position: absolute;
                    top: 50%;
                    left: 42%;
                    transform: translate(-42%, -50%);
                    color: $ms-color-themeDark;

                    // &:link, &:visited, &:active, &:hover {
                    //     background-color: $ms-color-white;
                    //     color: $ms-color-themeDark;
                    //     border: 2px solid $ms-color-themePrimary;
                    // }

                }
            }
        }
        .ms-CommandButton-button:hover, .ms-CommandButton-splitIcon:hover {
            background-color: $ms-color-themeLighter;
            color: $ms-color-themeDarker;
            // border:1px solid $ms-color-themePrimary;
        }

        .ms-CommandButton+.ms-CommandButton {
            margin: 0px;
        }

        &.ctrl-preview {
            float: right;
            // max-width: 88px;
            // background-color: $ms-color-white;
            // margin: 4px;
        }
        &.ctrl-body {
            float: left;
            // max-width: 88px;
            // background-color: $ms-color-white;
            // margin: 4px;
            .ms-CommandButton {

                .ms-CommandButton-button {
                    background: $ms-color-white;

                }
            }
            .btn-toggle-me {
                display: inline-block;
            }
            .btn-toggle-team {
                display: none;
            }
            &.is-team {
                .btn-toggle-me {
                    display: none;
                }
                .btn-toggle-team {
                    display: inline-block;
                }
            }


        }
        &.ctrl-nav-left {
            display: block;
            max-width: 200px;
            padding-left: 8px;
            // background-color: $ms-color-white;

        }



    }

    .btn-Nav-open {
        .wdx-Canvas-body.is-open & {
            display: none;
        }
        display: block;
        &.ms-CommandButton {
            width: 32px;
            height: 32px;

            .ms-CommandButton-button {

                .ms-CommandButton-icon {
                    margin:0px;
                }
            }

        }
    }

    .btn-Nav-close {
        .wdx-Canvas-body.is-open & {

            display: block;
        }
        margin:0px;
        display: none;


        &.ms-CommandButton {

            width: 32px;
            .ms-CommandButton-button {
                width: 32px;
                .ms-CommandButton-icon {
                    margin:0px;
                }
            }


        }
    }

    .ms-CommandBar--navBar.wd-CommandBar-pivot {
        background-color: inherit;
        padding-right: 0px;
    }

    .btn-toggle-ctrls {

        background: $ms-color-white;
        height: 32px;
        width: 32px;

        .ms-CommandButton-button {
            color: $ms-color-themeDarker;
            background: $ms-color-white;
            border: 2px solid $ms-color-themeLighter;
        }
        & .ms-CommandButton-button:hover, & .ms-CommandButton-splitIcon:hover {
            background-color: $ms-color-white;
            color: $ms-color-themeDark;
            border: 2px solid $ms-color-themePrimary;


        }

        .ms-CommandButton+.ms-CommandButton {
            margin: 0px;
        }

        &.is-team {

            .btn-toggle-team {
                display: none;
            }
            .btn-toggle-me {

                display: block;
            }
        }
        .btn-toggle-team {

        display: block;

        &.ms-CommandButton {
                width: 32px;

                .ms-CommandButton-button {
                    width: 32px;
                    .ms-CommandButton-icon {
                        margin:0px;
                    }
                }

            }
        }

        .btn-toggle-me  {

            margin:0px;
            display: none;


            &.ms-CommandButton {

                width: 32px;
                .ms-CommandButton-button {
                    width: 32px;
                    .ms-CommandButton-icon {
                        margin:0px;
                    }
                }


            }
        }

    }

}