.phx-view-bkgrdColor{
    background-color: #d9d9d9;
    @media only screen and (max-width: $ms-screen-max-lg) {
        background-color: #FFFFFF;
    }
}

.phx-login {
    //border: 1px solid $ms-color-themeLighter;
    margin: 0;
    position: absolute;
    top: 40%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    padding:10px;
    text-align: center;
    //box-shadow:0 0 10px 6px rgba(0,0,0,.1);
    /*ADDED BY OSCAR FOR FRED'S LAYOUT*/
    width:1000px;
    height: 425px;
    background-color: #ffffff;
    // @media only screen and (max-width: $ms-screen-max-lg) {
    //     box-shadow:0 0 10px 6px rgba(0,0,0,0);
    //     border: none;
    // }
    /*ADDED BY OSCAR FOR FRED'S LAYOUT*/

    div.isLogin {
        display:inline-block;
        width:300px;
        padding: 15px 0;
        vertical-align: top;
        .header{

           position: relative;
            display: block;
            height: 40px;

            h1 {
                position: absolute;
                left: 0;
                top: 0;
                max-width: 50%;
                overflow: hidden
            }

            button{
                position: absolute;
                right: 0;
                top: 50%;
                text-align: right;
                max-width: 50%;
                overflow: hidden
            }
        }

        .flagColumns{
            -moz-column-count: 2;
            // -moz-column-gap: 60px;
            -webkit-column-count: 2;
            // -webkit-column-gap: 60px;
            column-count: 2;
            // column-gap: 60px;
            background-color: $ms-color-themeLighter;
            padding: 10px;
            margin: 10px 0;
            text-align: left;

            li {
                padding: 10px;
                // margin: 5px;
                font-size: $ms-font-size-l;
               list-style: none;
                cursor: pointer;
                height: 45px;
                div {
                    display:block;
                    background-position: 0;
                    padding-left:35px;
                }
            }

            li:hover {
                background-color: $ms-color-themePrimary;
            }

        }
    }

    div.isFiller {
        display:inline-block;
        width: 650px;
        @media only screen and (max-width: $ms-screen-max-lg) {
            display: none;
        }
    }
}

.phx-login-form {
   padding:10px;
   margin: 0;
   text-align: left;
   .ms-Label {
       font-weight: 300;
   }
   .ms-Spinner {
        position:absolute;
        left: 40%;
        top: 40%;
        transform: translate(-40%, -40%);
   }

   .ms-TextField-field{
        height: 40px;
        padding: 6px 12px 6px;
   }

   .ms-Button {
        -webkit-appearance: none;
        border-radius: 0;
   }
   .isBtn{
        padding: 15px 0;
        // input[type="submit"]{
        //     width: 100%;
        //     color: #ffffff;
        //     height: 40px;
        //     margin-bottom: 15px;
        // }
        ul {
            padding: 0 0 25px 0;
            margin: 0;
            li {
                display:inline-block;
                float: left;
                vertical-align: top;
                width: 50%;
                height: 25px;
                background-position: 100%;
                cursor: pointer
            }
        }
   }

}
