#loading-bar {
    pointer-events: none;
    -webkit-pointer-events: none;
    -webkit-transition: 350ms linear all;
    -moz-transition: 350ms linear all;
    -o-transition: 350ms linear all;
    transition: 350ms linear all;
    .ng-enter {
      opacity: 0;
      .ng-enter-active{
          opacity: 1;
      }
    }
    .ng-leave{
      opacity: 1;
      .ng-leave-active{
          opacity: 0;
      }
    }
    .bar{
      -webkit-transition: width 350ms;
      -moz-transition: width 350ms;
      -o-transition: width 350ms;
      transition: width 350ms;
    
      background: $ms-color-themePrimary;
      position: fixed;
      z-index: 10002;
      top: 0;
      left: 0;
      width: 100%;
      height: 2px;
      border-bottom-right-radius: 1px;
      border-top-right-radius: 1px;
    }
    .peg{
      position: absolute;
      width: 70px;
      right: 0;
      top: 0;
      height: 2px;
      opacity: .45;
      -moz-box-shadow: $ms-color-themePrimary 1px 0 6px 1px;
      -ms-box-shadow: $ms-color-themePrimary 1px 0 6px 1px;
      -webkit-box-shadow: $ms-color-themePrimary 1px 0 6px 1px;
      box-shadow: $ms-color-themePrimary 1px 0 6px 1px;
      -moz-border-radius: 100%;
      -webkit-border-radius: 100%;
      border-radius: 100%;
    }
  }
  
  #loading-bar-spinner {
      pointer-events: none;
      -webkit-pointer-events: none;
      -webkit-transition: 350ms linear all;
      -moz-transition: 350ms linear all;
      -o-transition: 350ms linear all;
      transition: 350ms linear all;
      
      position: fixed;
      z-index: 10002;
      overflow: show;
      height: 40px;
      width: 40px;
      overflow: show;
      margin: auto;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      .ng-enter {
          opacity: 0;
          .ng-enter-active {
              opacity: 1;
          }
      }
      .ng-leave {
          opacity: 1;
          .ng-leave-active {
              opacity: 0;
          }
      }
      .spinner-icon {
          width: 40px;
          height: 40px;
        
          border:  solid 2px transparent;
          border-top-color:  $ms-color-themePrimary;
          border-left-color: $ms-color-themePrimary;
          border-radius: 50%;
        
          -webkit-animation: loading-bar-spinner 400ms linear infinite;
          -moz-animation:    loading-bar-spinner 400ms linear infinite;
          -ms-animation:     loading-bar-spinner 400ms linear infinite;
          -o-animation:      loading-bar-spinner 400ms linear infinite;
          animation:         loading-bar-spinner 400ms linear infinite;
      }
  }
  
  #loading-bar-spinner::before{
      content: '';
      display: block;
      position: fixed;
      top: 2px;
      left: 0;
      width: 100%;
      height:100%;
      background-color: #ffffff;
      -khtml-opacity:.50; 
      -moz-opacity:.50; 
      -ms-filter:alpha(opacity=50);
       filter:alpha(opacity=50);
       filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0.5);
       opacity:.50; 
  }
  
  @-webkit-keyframes loading-bar-spinner {
    0%   { -webkit-transform: rotate(0deg);   transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  @-moz-keyframes loading-bar-spinner {
    0%   { -moz-transform: rotate(0deg);   transform: rotate(0deg); }
    100% { -moz-transform: rotate(360deg); transform: rotate(360deg); }
  }
  @-o-keyframes loading-bar-spinner {
    0%   { -o-transform: rotate(0deg);   transform: rotate(0deg); }
    100% { -o-transform: rotate(360deg); transform: rotate(360deg); }
  }
  @-ms-keyframes loading-bar-spinner {
    0%   { -ms-transform: rotate(0deg);   transform: rotate(0deg); }
    100% { -ms-transform: rotate(360deg); transform: rotate(360deg); }
  }
  @keyframes loading-bar-spinner {
    0%   { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }