@mixin anim-verticle {
    -webkit-transition: max-height 320ms cubic-bezier(0.65, 0.05, 0.36, 1);
    -moz-transition: max-height 320ms cubic-bezier(0.65, 0.05, 0.36, 1);
    -o-transition: max-height 320ms cubic-bezier(0.65, 0.05, 0.36, 1);
    transition: max-height 320ms cubic-bezier(0.65, 0.05, 0.36, 1);
}