.wd-panel {
    margin-top: 0px;
    overflow: hidden;
    padding: 0px;
    width: 100%;
    height: calc(100vh - 102px);
    &::-webkit-scrollbar {
        display: none;
    }


    // .ms-Grid-col {
    //     width: 100%;
    //     min-width:260px;
    // }
    .panel-block {

        width: 100%;
        margin: 10px 0px;
        padding: 8px;
        background: $ms-color-themeLighterAlt;
        h2 {
                margin-top: 2px;
                color: $ms-color-themeDark;
                font-weight: 300;
                font-size: 21px;


        }
    }
}