.left-nav-filter {
    position: fixed;
    bottom: 2px;
    left: 0;
    width: 200px;

    ul.nav {
        position: relative;
       -webkit-transition: all 500ms ease;
       -o-transition: all 500ms ease;
       transition: all 500ms ease;
       margin: 0;
       padding: 0;
       list-style: none;

       ul {
           margin: 0;
           padding: 0;
           list-style: none;
       }

    //    li {
    //        border-bottom: 1px solid $ms-color-themeLighter;
    //    }
       .subMenu {

           a {
               white-space: nowrap;
               overflow: hidden;
               text-overflow: ellipsis;
           }
       }

       a {
           position: relative;
           display: block;
           text-decoration: none;
           color: rgba(0, 0, 0, .75);
           padding: 15px;
           color: $ms-color-themeDarker;
           &:link {
               text-decoration: none;
               color: $ms-color-themeDarker;
           }
           &:hover {
               text-decoration: none;
               background-color: $ms-color-themeLight;
           }
       }

       .ms-Icon {
           margin-top: 4px;
       }

       .nextIco {
           position: absolute;
           top: 50%;  /* position the top  edge of the element at the middle of the parent */
           right: 15px; /* position the left edge of the element at the middle of the parent */
           transform: translate(-50%, -50%);
       }

       .backIco {
           position: absolute;
           top: 50%;  /* position the top  edge of the element at the middle of the parent */
           left: 15px; /* position the left edge of the element at the middle of the parent */
           transform: translate(-50%, -50%);
       }
       .js-back {
           padding: 15px;
           cursor: pointer;
           color: rgba(0, 0, 0, .75);
           text-align:center;
           position: relative;
           &:hover {
               color: $ms-color-themeDarker;
               background-color: $ms-color-themeLight;
           }
       }

   }


}

.filterbox {
    @include ms-clearfix;
    padding: 10px;
    margin: 0px 0;
    position: fixed;
    bottom: 0px;
    right:20px;
    // left: 200px;
    width: calc(100% - 240px);

    // max-height: 120px;
    border: 1px solid $ms-color-themeLighter;
    border-bottom: 0px;
    background: $ms-color-themeLighterAlt;
    overflow-y: scroll;
    -ms-overflow-style: none;
    overflow: auto;
    overflow-x: hidden;
    ul {
        list-style: none;
        padding-left: 0px;
    }
}