.font-table th,
.font-table td,
.font-table-cell {
  display: table-cell;
  padding: .6em 4px;
  vertical-align:top;
}

.font-table tr, .file-table tr {
 border-bottom: 1px solid #eaeaea;
}

.font-table thead th, .file-table thead th {
  font-weight: 700;
}

.file-table th,
.file-table td,
