.settingsWrap {

    .btn-settings {

        .ms-CommandButton-button {
            width: 44px;


            &:hover, &:focus {
                outline: none;
                color: $ms-color-themeDarker;
                background-color: $ms-color-themeLighter;
                border: 0px solid;
                outline: none;
            }
            .ms-CommandButton-icon {
                margin:0px;
                .ms-Icon {
                    color: $ms-color-themePrimary;

                    &:hover {

                        outline:0px;
                        color: $ms-color-themeDarker;
                        background-color: $ms-color-themeLighter;
                        outline: none;
                        border: 0px solid;


                    }
                }
            }

        }
    }
    .ms-Panel-contentInner {
        padding-bottom: 60px;
    }

}

.wd-Panel--settings {

    overflow: auto;
    overflow-y: scroll;
    overflow-x: hidden;


     &::-webkit-scrollbar {
        display: none;
    }


}
.wd-Panel-settings {

    margin-top: 0px;
    overflow-y: scroll;
    -ms-overflow-style: none;
    overflow: auto;
    overflow-x: hidden;
    padding: 0px;
    &::-webkit-scrollbar {
        display: none;
    }

    .ms-Panel-headerText {
        margin-top: 0px;
        color: $ms-color-themePrimary;

    }
    h2.ms-Panel-headerText {
        margin-top: 20px;
        color: $ms-color-themePrimary;
    }

    .ms-Grid-col {
        min-width: 300px;
        width: 50%;
        padding: 0px 20px;

    }


    .ms-Toggle.ms-Toggle--textLeft {
        width: 80%;

        margin-bottom: 12px;
        .ms-Toggle-description {
            min-width: 150px;
            max-width:80%
        }
    }

}

